import React from "react";
import Slider from "react-slick";
import slide1 from "../../assets/m1.png";
import slide2 from "../../assets/m3.png";
import slide3 from "../../assets/m2.png";
import sliderBg from "../../assets/monitor.png";
import SliderPrev from "./SliderPrev";
import SliderNext from "./SliderNext";
import { ReactComponent as StatsColoredIcon } from "../../assets/stats-colored.svg";
import { ReactComponent as RateIcon } from "../../assets/exchange-rate.svg";
import { ReactComponent as ChipIcon } from "../../assets/casino-chip.svg";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const settings = {
  slidesToShow: 1,
  slidesToScroll: 1,
  dots: true,
  infinite: true,
  speed: 1000,
  fade: true,
  autoplay: true,
  autoplaySpeed: 2000,
  nextArrow: <SliderNext />,
  prevArrow: <SliderPrev />,
};

const Carousel = () => {
  return (
    <div id="features">
      <div className="features__container">
        <img className="features__monitor" src={sliderBg} alt="Monitor bg" />
        <Slider className="features__slider" {...settings}>
          <div className="item">
            <div className="slider__descr">
              <h2>Earn Massive Revenue</h2>
              <p>
                Join the most rewarding affiliate program today and turn your
                traffic into cash.
              </p>
              <ul className="mainList">
                <li>
                  <StatsColoredIcon />
                  Earn Up To 50% Revenue Share
                </li>
                <li>
                  <ChipIcon />
                  Popular Casino Brands
                </li>
                <li>
                  <RateIcon />
                  High Conversion Rates
                </li>
              </ul>
            </div>
            <div className="slider__image">
              <img className="lazy" src={slide1} alt="Featured image 1" />
            </div>
          </div>

          <div className="item">
            <div className="slider__descr">
              <h2>Promote Today</h2>
              <p>
                Who wants to wait for lengthy approval processes? Sign up in
                seconds and start earning now.
              </p>
              <ul className="mainList">
                <li>Fast Payouts</li>
                <li>High end Industry Driven Brands</li>
                <li>No Fuss</li>
              </ul>
            </div>
            <div className="slider__image">
              <img className="lazy" src={slide2} alt="Featured image 2" />
            </div>
          </div>

          <div className="item">
            <div className="slider__descr">
              <h2>Powerful Tools</h2>
              <p>
                Visualise your earnings in a brand new way with our custom built
                affiliate platform. Designed for your convenience.
              </p>
              <ul className="mainList">
                <li>High Converting Creatives</li>
                <li>Cutting Edge Reporting</li>
                <li>Instant Analytics Across Multiple Cost Metrics</li>
              </ul>
            </div>
            <div className="slider__image">
              <img className="lazy" src={slide3} alt="Featured image 3" />
            </div>
          </div>
        </Slider>
      </div>
    </div>
  );
};

export default Carousel;
